
body, html {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

:root {
  --color-background-light: #ffffff;
  --color-navbar-background-light: #F9F5F6;
  --color-text-light: #1d1d1d;

  --color-background-dark: #1d1d1d;
  --color-navbar-background-dark: #121212;
  --color-text-dark: #ffffff;

  --color-primary: #1877f2;
  --color-secondary: #4267b2;

  --font-family: Arial, sans-serif;
}

.light-theme {
  background-color: var(--color-background-light);
  color: var(--color-text-light);
}

.dark-theme {
  background-color: var(--color-background-dark);
  color: var(--color-text-dark);
}

* {
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
}

body {
  font-family: var(--font-family);
}

nav {
  padding: 20px;
  display: flex;
  user-select: none;
  justify-content: space-around;
}

nav a {
  color: inherit;
  text-decoration: none;
}

nav button {
  background: none;
  border: none;
  cursor: pointer;
}

h1, h2, h3 {
  font-weight: normal;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.2em;
}

p {
  line-height: 1.5;
  margin-bottom: 1em;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 20px;
}

.home-page {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: left;
  position: relative;
  padding: 20px;
}

.home-page-description {
  position: absolute;
  top: 0;
}

.dark-theme .home-page-mainly-syntax {
  background-color: rgb(14, 14, 15); 
  border-radius: 20px;
  padding: 20px;
}

.light-theme .home-page-mainly-syntax {
  background-color: rgb(229, 229, 234); 
  border-radius: 20px;
  padding: 20px;
}

.home-page-mainly-button {
  background-color: #ffffff00;
  border: none;
}

.home-page-mainly-title {
  user-select: none;
}

.home-page-mainly-button img {
  margin-left: 220px;
}

.home-page-content {
  max-width: 50%;
}

.home-page-title {
  font-size: 2.5em;
  font-weight: bold;
  margin-bottom: 20px;
}

.home-page-subtitle {
  font-size: 1.2em;
  padding-left: 50px;
  color: cadetblue;
  margin-bottom: 30px;
}

.logoImage {
  width: 300px;
  height: 300px;
  user-select: none;
  border-radius: 50%;
}

.home-page-reminder {
  font-size: 1.5em;
  padding-top: 300px;
  font-weight: bold;
  margin-bottom: 20px;
}

.home-page-quote {
  font-size: 1em;
  color: var(--color-secondary);
  cursor: pointer;
  margin-bottom: 30px;
}

.home-page-reminder, .home-page-quote {
  padding-left: 40px;
}

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#content {
  flex: 1 0 auto;
}

.dark-theme footer {
  background-color: var(--color-navbar-background-dark);
  
}

.light-theme footer {
  background-color: var(--color-navbar-background-light);
}

footer {
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-shrink: 0;
}

.footer-links,
.footer-subdomains,
.footer-email {
  display: flex;
  flex-direction: column;
}

.footer-links a,
.footer-subdomains a,
.footer-email a {
  margin-bottom: 10px;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-title {
  text-decoration: underline;
  font-weight: 600;
}

.light-theme .footer-links a,
.light-theme .footer-subdomains a,
.light-theme .footer-email a {
  color: var(--color-text-light);
}

.dark-theme .footer-links a,
.dark-theme .footer-subdomains a,
.dark-theme .footer-email a {
  color: var(--color-text-dark);
}

.context-menu {
  background-color: var(--color-navbar-background-light);
  border: 1px solid var(--color-text-light);
  border-radius: 5px;
  padding: 10px;
  width: 150px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1000;
}

.context-menu button {
  display: block;
  background: none;
  color: var(--color-text-light);
  text-align: left;
  border: none;
  width: 100%;
  padding: 5px 0;
  cursor: pointer;
  font-size: 1em;
}

.dark-theme .context-menu button:hover {
  background-color: #000000;
}

.light-theme .context-menu button:hover {
  background-color: #e4e0e1;
}

.dark-theme .context-menu {
  background-color: var(--color-navbar-background-dark);
  border-color: var(--color-text-dark);
}

.dark-theme .context-menu button {
  color: var(--color-text-dark);
}

.services-page {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 20px;
  flex-wrap: wrap;
}

.services-title-page {
  font-size: xx-large;
  text-align: center;
  width: 100%;
}

.dark-theme .services-button-ai-page {
  cursor: pointer;
  background-color: #0e0e0f;
  border: #555 2px solid;
  padding: 8px 30px;
  border-radius: 0 0 15px 15px;
  color: #ffffff;
  transition: all 0.2s;
}

.dark-theme .services-button-ai-page:hover {
  background-color: #212122;
  border-radius: 0 0 10px 10px;
}

.light-theme .services-button-ai-page {
  cursor: pointer;
  background-color: #f0f0f4;
  border: #ffffff 2px solid;
  padding: 8px 30px;
  border-radius: 0 0 15px 15px;
  color: #000000;
  transition: all 0.2s;
}

.light-theme .services-button-ai-page:hover {
  background-color: #dddde3;
  border-radius: 0 0 10px 10px;
}

.dark-theme .services-disable-button-ai-page {
  cursor:not-allowed;
  background-color: #0e0e0f;
  border: #555 2px solid;
  padding: 8px 30px;
  border-radius: 0 0 15px 15px;
  color: #ffffff;
  transition: all 0.2s;
}

.light-theme .services-disable-button-ai-page {
  background-color: #f0f0f4;
  cursor:not-allowed;
  border: #ffffff 2px solid;
  padding: 8px 30px;
  border-radius: 0 0 15px 15px;
  color: #000000;
}

.services-ai-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-radius: 15px;
  user-select: none;
  margin-top: 20px;
}

.dark-theme .services-ai-page {
  background-color: #131414;
}

.light-theme .services-ai-page {
  background-color: #e4e0e1;
}

.about-us-page {
  padding: 20px;
}

.about-us-page h1 {
  font-size: 2.5em;
  margin-bottom: 1em;
  text-decoration: underline;
}

.about-us-page h2 {
  font-size: 2em;
  margin: 2em 0 1em;
}

.about-us-page p {
  line-height: 1.5;
  margin-bottom: 1em;
}

.about-us-page a {
  color: var(--color-primary);
  text-decoration: none;
}

.about-us-page a:hover {
  text-decoration: underline;
}

.about-us-page ul {
  padding-left: 1em;
}

.about-us-page li {
  margin-bottom: 0.5em;
}

.dark-theme::-webkit-scrollbar {
  color: #ffffff
}

::-webkit-scrollbar {
  width: 8px; 
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

hr {
  width: 100%;
}

.dark-theme hr {
  color: #ffffff;
}

.light-theme hr {
  color: #000000;
}

.NotFound-Page {
  text-align: center;
  align-items: center;
  align-self: center;
}

.NotFound-Button-Page {
  padding: 15px 30px;
  transition: all 0.2s;
  font-weight: 530;
}

.dark-theme .NotFound-Button-Page {
  background-color: #131414;
  color: #fff;
  border: none;
  border-radius: 15px 0 15px 0;
}

.dark-theme .NotFound-Button-Page:hover {
  background-color: #111010;
  border-radius: 0 15px 0 15px;
}

.light-theme .NotFound-Button-Page {
  background-color: #e8e4e6;
  color: #000;
  border: none;
  border-radius: 15px 0 15px 0;
}

.light-theme .NotFound-Button-Page:hover {
  background-color: #ededf0;
  border-radius: 0 15px 0 15px;
}

.radius-image {
  border-radius: 50px;
}

@import url('https://fonts.cdnfonts.com/css/coarse-rounded');

.CoarseRounded {
  font-family: 'Coarse Rounded', sans-serif;
}

.center {
  text-align: center;
}

.text2image-page {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.text2image-page.CoarseRounded {
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 20px;
}

.text2image-input {
  outline: none;
}

.dark-theme .text2image-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #191717;
  background-color: #0e0e0f;
  color: white;
  border-radius: 4px;
  margin-bottom: 20px;
}

.light-theme .text2image-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #e3dcdc;
  background-color: #e7e7ee;
  color: black;
  border-radius: 4px;
  margin-bottom: 20px;
}

.dark-theme .text2image-button {
  background-color: #000;
  color: #fff;
  padding: 10px;
  border: 1px solid #000;
  border-radius: 4px;
  cursor: pointer;
}

.light-theme .text2image-button {
  background-color: #fff;
  color: #000;
  padding: 10px;
  border: 1px solid #e7e2e2;
  border-radius: 4px;
  cursor: pointer;
}

.text2image-image {
  max-width: 100%;
  border-radius: 17px;
  margin-top: 20px;
  margin-left: 30%;
}

.text2image-button {
  margin-left: 35%;
}

@media (max-width: 768px) {
  .text2image-page {
    max-width: 100%;
  }

  .text2image-input,
  .text2image-button {
    min-width: 20%;
  }
}

.loading-dots {
  display: inline-block;
  width: 30px;
  text-align: center;
  margin-left: 42.5%;
}

.light-theme .loading-dots div {
  background-color: #eaeafb;
}

.dark-theme .loading-dots div {
  background-color: #3f3f42;
}

.loading-dots div {
  display: inline-block;
  width: 6px;
  height: 6px;
  margin: 0 2px;
  border-radius: 50%;
  animation: bounce 1.5s infinite;
}

.loading-dots div:nth-child(1) {
  animation-delay: 0.1s; 
}

.loading-dots div:nth-child(2) {
  animation-delay: 0.3s; 
}

.loading-dots div:nth-child(3) {
  animation-delay: 0.5s; 
}

.loading-dots div:nth-child(4) {
  animation-delay: 0.7s; 
}

.loading-dots div:nth-child(5) {
  animation-delay: 0.9s;
}

.loading-dots div:nth-child(6) {
  animation-delay: 1.2s; 
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.early-access-page {
  font-family: sans-serif;
  overflow: scroll;
  max-height: 50%;
  min-height: 30%;
  height: 40%;
  width: 35%;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
  border-radius: 15px;
  padding: 50px;
}

.light-theme .early-access-page {
  color: #666;
  background-color: #f9f5f6;
}

.dark-theme .early-access-page {
  color: #e2dfdf;
  background-color: #121212;
}

.early-access-text {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-top: 0;
}

.early-access-form {
  margin-top: 20px;
  width: 100%;
}

.light-theme .early-access-input,
.light-theme .early-access-textarea {
  border: 1px solid #ccc;
  background-color: #fff;
  color: black;
}

.dark-theme .early-access-input,
.dark-theme .early-access-textarea {
  border: 1px solid #2c2a2a;
  background-color: #1d1d1d;
  color: white;
}

.early-access-input,
.early-access-textarea {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
}

.light-theme .early-access-button {
  background-color: #e4e0e1;
  color: black;
}

.dark-theme .early-access-button {
  background-color: #1b1b1c;
  color: white;
}

.early-access-button {
  padding: 10px 20px;
  width: 100%;
  margin-left: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.early-access-input:focus,
.early-access-textarea:focus {
  outline: none;
}

.dark-theme .early-access-button:hover {
  background-color: #252527
}

.light-theme .early-access-button:hover {
  background-color: #ded9d9;
}

.letscheck {
  overflow: auto;
}